import { Typography, Table } from "antd";
import React from "react";

const Users = () => {
  const { Title } = Typography;

  const data = [
    {
      sno: "1",
      name: "Muhammad hasnain",
      email: "themohdhasnain@gmail.com",
      DOB: "10/2/2002",
      member_since: "05/1/2023",
    },
    {
      sno: "2",
      name: "ALi hasnain",
      email: "coding@gmail.com",
      DOB: "10/2/2002",
      member_since: "05/1/2023",
    },
  ];

  const columns = [
    {
      title: "SNO.#",
      dataIndex: "sno",
      key: "sno",
    },
    {
      title: "Name",
      dataIndex: "name",
      key: "name",
    },
    {
      title: "Email",
      dataIndex: "email",
      key: "email",
    },
    {
      title: "DOB",
      dataIndex: "DOB",
      key: "DOB",
    },
    {
      title: "Member Since",
      dataIndex: "member_since",
      key: "member_since",
    },
  ];

  const dataSource = [
    ...data.map((elem, i) => {
      return {
        key: i + 1,
        sno: i + 1,
        email: elem.email,
        name: elem.name,
        DOB: elem.DOB,
        member_since: elem.member_since,
      };
    }),
  ];

  return (
    <div>
      <Title level={window.matchMedia("(max-width: 600px)").matches ? 4 : 3}>
        All Registered Users
      </Title>
      <Table
        dataSource={dataSource}
        columns={columns}
        className="responsiveTable"
      />
      ;
    </div>
  );
};

export default Users;
