import React from "react";
import { SendOutlined } from "@ant-design/icons";

const Chat = () => {
  return (
    <section className="d-flex justify-content-between Chat-section">
      <div className="chats_list w-25 border py-2">
        <p className="fs-5 fw-bold px-2 py-2 border-bottom mb-3">Chats</p>
        <div className="user-chat d-flex align-items-center justify-content-between px-3">
          <div
            className="avatar"
            style={{
              background: `url("https://fiverr-res.cloudinary.com/t_profile_original,q_auto,f_auto/attachments/profile/photo/492a0f9752b97f1a58dd6e852505e2d8-1638841169105/fef144a5-f0c8-4585-afcf-b8488f3e3b52.jpg")`,
            }}
          ></div>
          <div className="chat-info">
            <p className="fs-6">Ali</p>
            <p className="text-muted last_msg">
              Me: Follow the guidelines above{" "}
            </p>
          </div>
        </div>

        <div className="user-chat d-flex align-items-center justify-content-between mt-3 px-3">
          <div
            className="avatar"
            style={{
              background: `url("https://fiverr-res.cloudinary.com/t_profile_original,q_auto,f_auto/attachments/profile/photo/1f8a697e4cd3938fb1129bacdf8e4601-1664508849655/c1365d67-74fe-48b6-9ba5-daadc96a3470.jpg")
                `,
            }}
          ></div>
          <div className="chat-info">
            <p className="fs-6">Raza</p>
            <p className="text-muted last_msg">Me: How can I help you? </p>
          </div>
        </div>
      </div>
      <div className="chat-convo w-75 ms-3 border border-l-0 py-2">
        <div className="header border-bottom pb-2 px-2">
          <div className="d-flex align-items-center">
            <div
              className="avatar sm-avatar"
              style={{
                background: `url("https://fiverr-res.cloudinary.com/t_profile_original,q_auto,f_auto/attachments/profile/photo/1f8a697e4cd3938fb1129bacdf8e4601-1664508849655/c1365d67-74fe-48b6-9ba5-daadc96a3470.jpg")
                `,
              }}
            ></div>
            <p className="ms-2">Muhammad</p>
          </div>

          <div className="send d-flex">
            <input
              type={"text"}
              className="form-control rounded-0"
              placeholder="Enter message here!"
            />
            <button className="btn_primary px-3 h-100 w-auto">
              <SendOutlined style={{color: "white", fontSize: "1.3rem"}} />
            </button>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Chat;
